export default ".weather-widget_wrapper{font-family:\"Montserrat\";width:370px;max-width:100%;color:#fff;box-sizing:border-box;font-size:20px;font-weight:500;cursor:default}.weather-widget{width:100%;min-height:200px;text-shadow:4px 2px 2px rgba(0,0,0,.6)}.weather-widget_single{position:relative;padding-top:15px;background-color:rgba(21,22,23,.7);padding:15px 20px 15px;border-radius:20px;margin-bottom:10px}.weather-widget_single:before{content:\"\";position:absolute;width:100%;height:100%;left:0;bottom:0;z-index:-1;border-radius:20px;background-image:linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0.6) 70%, rgba(0, 0, 0, 0.7) 80%, rgba(0, 0, 0, 0.8) 90%, rgba(0, 0, 0, 0.9) 95%, rgb(0, 0, 0) 100%)}.weather-widget_list{width:100%;height:100%;z-index:1;box-sizing:inherit;background-color:rgba(21,22,23,.7);border-radius:20px;padding:15px 20px 15px}.weather-widget_head{display:flex;justify-content:space-between;align-items:flex-start}.weather-widget_heading{margin:0;font-size:30px;font-weight:500;line-height:30px}.weather-widget_main{font-size:45px;padding:50px 15px 25px 10px;box-sizing:border-box;font-weight:600;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.weather-widget_img{width:180px;margin-bottom:20px}.weather-widget_icon{width:30px;height:30px;display:block;background-color:rgba(0,0,0,0);background-size:contain;background-repeat:no-repeat;width:25px;margin:0 5px 0 10px;height:25px}.weather-widget_btn{width:30px;height:30px;background-color:rgba(0,0,0,0);background-size:contain;background-repeat:no-repeat;border:0;transition:transform .5s;cursor:pointer}.weather-widget_btn:hover{transform:scale(1.1)}.weather-widget_btn.add{width:20px;height:20px}.weather-widget_btn.delete{width:20px;height:20px}.weather-widget_btn.absolute{position:absolute;top:15px;right:20px;z-index:2;border-radius:50%}.weather-widget_info{padding:25px 0;box-sizing:border-box}.weather-widget_row{display:flex;justify-content:flex-start;margin-top:25px}.weather-widget_row:first-child{margin-top:0}.weather-widget_item{display:flex;align-items:center;margin-right:10px;font-size:20px;letter-spacing:2px}.weather-widget_item:last-child{margin-right:0}.weather-widget_body{margin-top:25px}.weather-widget_body .city-item{display:flex;align-items:center;justify-content:space-between;width:100%;padding:5px 10px;box-sizing:border-box;margin-bottom:5px;border:1px solid rgba(255,255,255,.4);cursor:grab}.weather-widget_body .city-item:last-child{margin-bottom:0}.weather-widget_input-wrapper{display:flex;justify-content:space-between;align-items:center;padding:5px 10px;width:100%;border:1px solid rgba(255,255,255,.4);margin-top:20px;box-sizing:border-box}.weather-widget_input{border:0;flex:1;background-color:inherit;color:#fff;font-size:14px;font-weight:600}.weather-widget_input:focus{outline:none}.no-cities{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:100%;max-width:350px;box-sizing:border-box;background-color:rgba(21,22,23,.9);border-radius:20px;padding:15px 20px 15px}.no-cities_btn{background-color:rgba(0,0,0,0);color:#fff;border:1px solid #fff;padding:10px 20px;box-sizing:border-box;margin-top:20px;cursor:pointer}.d-none{display:none}"